@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond:ital,wght@0,400;0,600;1,300;1,400;1,600&display=swap);
:root{--font1:  "Times New Roman", Times, serif;--font2: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;--font3: 'Cormorant Garamond', "Times New Roman", Times, serif;--white: #ffffff;--black: #000;--offBlack: #232323;--greyLight: #EBEBEB;--greyMed-1: #707070;--greyDark: #212121;--grey-4: #f7f9fa;--black-3: #000f31}*,*:before,*:after{padding:0;margin:0;box-sizing:border-box}html{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:16px}li{list-style:none !important}a{text-decoration:none !important}a:visited{color:#000;color:var(--black)}img{display:block;max-width:100%;height:auto}section{width:100%}.slider-container{margin:10px auto;display:grid;grid-template-columns:auto auto auto;width:100%;max-width:1200px}@media all and (min-width: 1301px){.slider-container{height:1080px}}@media all and (min-width: 801px) and (max-width: 1300px){.slider-container{height:880px}}@media all and (min-width: 601px) and (max-width: 800px){.slider-container{height:710px}}@media all and (max-width: 600px){.slider-container{height:500px}}.slider-container .slider{margin:auto;height:auto;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.slider-container .right-container,.slider-container .left-container{display:-webkit-flex;display:flex;padding:0 20px}@media all and (max-width: 600px){.slider-container .right-container,.slider-container .left-container{padding:0 10px}}.image{object-fit:fill;margin:0 auto}.slide h1{font-weight:300;font-style:italic;text-transform:capitalize;font-size:21px}@media all and (max-width: 650px){.slide h1{font-size:14px}}.slide .line{vertical-align:middle;content:'';display:inline-block;background:#000;height:1px;width:60px;margin-left:0.6em;margin-right:0.6em}@media all and (max-width: 700px){.slide .line{width:40px}}.make-lower{text-transform:lowercase}.slide.portrait h1{margin:15px auto 0 auto}@media all and (min-width: 1301px){.slide.portrait h1{width:50%}}@media all and (min-width: 801px) and (max-width: 1300px){.slide.portrait h1{width:45%}}@media all and (min-width: 501px) and (max-width: 800px){.slide.portrait h1{width:60%}}@media all and (max-width: 500px){.slide.portrait h1{width:80%}}.slide.portrait img{margin:0 auto}@media all and (min-width: 1301px){.slide.portrait img{width:50%}}@media all and (min-width: 801px) and (max-width: 1300px){.slide.portrait img{width:45%}}@media all and (min-width: 501px) and (max-width: 800px){.slide.portrait img{width:60%}}@media all and (max-width: 500px){.slide.portrait img{width:80%}}.slide.landscape h1{margin:10px auto 0 auto}@media all and (min-width: 1301px){.slide.landscape h1{width:90%}}@media all and (min-width: 701px) and (max-width: 1300px){.slide.landscape h1{width:70%}}@media all and (max-width: 700px){.slide.landscape h1{width:96%}}@media all and (min-width: 1301px){.slide.landscape img{width:90%}}@media all and (min-width: 701px) and (max-width: 1300px){.slide.landscape img{width:70%}}@media all and (max-width: 700px){.slide.landscape img{width:96%}}.arrow{width:30px;margin:auto}@media all and (max-width: 1300px){.arrow{width:25px}}@media all and (max-width: 600px){.arrow{width:20px}}@media all and (max-width: 450px){.arrow{width:10px}}.arrow img{transition:all 0.3s}.arrow:hover img{cursor:pointer;-webkit-transform:scale(0.9);transform:scale(0.9)}@media all and (max-width: 600px){.arrow:hover img{-webkit-transform:scale(1);transform:scale(1)}}.slide{opacity:0;transition:all 1.4s ease-in}.slide.active{opacity:1;transition:all 1.4s ease-in}.dots-container{font-size:1.4rem;font-weight:300;z-index:40;width:100px;margin:auto;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.dot{padding:0 12px;cursor:pointer;font-size:22px;position:relative}.dot:hover{font-style:italic}@media all and (max-width: 700px){.dot{font-size:14px}}.dot.active::after{content:'';position:absolute;width:100%;height:1px;background-color:#000;transition:background-color 0.3s ease-in-out;bottom:-3px;left:0;right:0;margin:auto}@media all and (max-width: 700px){.dot.active::after{width:60%}}.pagination-container{display:-webkit-flex;display:flex;width:100%}

